<template>
    <div class="progressTest">
        <div class="title">
            <h3>Form</h3>
            <p></p>
        </div>
        <div class="usage">
            <h3 class="title"></h3>
            <p></p>
            <div class="useProgress">
                <dl-the-form :formhead="formhead" :formname="formname" :currentaddress="currentaddress" :companyaddress="companyaddress" :email="email" :Mobilephonenumber="Mobilephonenumber" :QQno="QQno" :homeaddress="homeaddress" :selects="selects"></dl-the-form>
            </div>
            <div class="content">
                <div class="code">
                    <p><span><</span>dl-the-form</p>
                    <p>&nbsp;&nbsp;:formhead="formhead"</p>
                    <p>&nbsp;&nbsp;:formname="formname"</p>
                    <p>&nbsp;&nbsp;:currentaddress="currentaddress"</p>
                    <p>&nbsp;&nbsp;:companyaddress="companyaddress"</p>
                    <p>&nbsp;&nbsp;:email="email"</p>
                    <p>&nbsp;&nbsp;:Mobilephonenumber="Mobilephonenumber"</p>
                    <p>&nbsp;&nbsp;:QQno="QQno"</p>
                    <p>&nbsp;&nbsp;:homeaddress="homeaddress"</p>
                    <p>&nbsp;&nbsp;:selects="selects"<span>></span></p>
                    <p><span><</span>/dl-the-form<span>></span></p>
                    <p class="top"><span><</span>script<span>></span></p>
                    <p>&nbsp;&nbsp;export default {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;data() {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;formhead:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;formname:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;currentaddress:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;companyaddress:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;email:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Mobilephonenumber:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;QQno:"QQ",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;homeaddress:"",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;selects:["","","","","","",""]</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;}</p>
                    <p><span><</span>/script<span>></span></p>
                </div>
            </div>
            <div class="show" @click="show"></div>
        </div>
        <div class="usage">
            <h3 class="title">Attributes</h3>
            <div class="attributesTable">
                <table>
                    <thead>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>formhead</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>formname</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>currentaddress</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>companyaddress</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>email</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>Mobilephonenumber</td>
                        <td></td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>QQno</td>
                        <td>QQ</td>
                        <td>String</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>homeaddress</td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>selects</td>
                        <td>select</td>
                        <td>String</td>
                        <td></td>
                        <td>(||||||)</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    export default {
        name: "form",
        data(){
            return{
                formhead:"",
                formname:"",
                currentaddress:"",
                companyaddress:"",
                email:"",
                Mobilephonenumber:"",
                QQno:"QQ",
                homeaddress:"",
                selects:["","","","","","",""]
            }
        },
        methods:{
            show(){
                $(".content").toggle();
            }
        }
    }
</script>

<style scoped>
    .bottom{
        margin-bottom: 50px;
    }
    .progressTest{
        margin-top: 50px;
    }
    .title>h3{
        font-size: 28px;
        font-weight: 400;
        color: #1f2f3d;
        margin-bottom: 15px;
    }
    .title>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .usage>h3{
        font-size: 22px;
        font-weight: 400;
        color: #1f2f3d;
        margin-top: 55px;
        margin-bottom: 15px;
    }
    .usage>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .useProgress{
        border: 1px solid #ebebeb;
        padding: 24px;
    }
    .content{
        background-color: #fafafa;
        padding: 10px;
        border: 1px solid #ebebeb;
        border-top: none;
        display: none;
    }
    .top{
        margin-top: 10px;
    }
    .description{
        border: 1px solid #ebebeb;
        background-color: #fff;
        line-height: 22px;
        padding: 20px;
        border-radius: 3px;
        font-size: 14px;
        color: #666;
    }
    .description>span{
        color: #5e6d82;
        background-color: #e6effb;
        margin: 0 4px;
        display: inline-block;
        padding: 1px 5px;
        font-size: 12px;
        border-radius: 3px;
        height: 18px;
        line-height: 18px;
    }
    .code{
        padding: 20px 10px 10px;
        color: #3182bd;
        font-size: 12px;
    }
    .code>p{
        line-height: 20px;
    }
    .show{
        border: 1px solid #ebebeb;
        border-top: none;
        font-size: 14px;
        line-height: 44px;
        text-align: center;
        color: #5e6d82;
    }
    table{
        width: 100%;
        font-size: 14px;
        margin-bottom: 30px;
    }
    table tr{
        border-bottom: 1px solid #ebebeb;
    }
    table td{
        padding: 15px;
        color: #606266;
    }
    thead td{
        color: #909399;
    }
</style>


