<template>
  <div class="selectlist">
    <div class="title">
      <h3>Select </h3>
      <p></p>
    </div>
    <div class="select-eg">
      <p>
        <dl-select :selectWidth="selectWidth"
              :subject="subject"
              :selectColorbg="selectColorbg"
              :selectContent="selectContent"
              :selectHeight="selectHeight"
              :selectFont="selectFont"
              @changeSelect="datasss">
        </dl-select>
      </p>

    </div>
      <div class="show-demo">
        <h5  @click="handleChangeTag"></h5>
        <div class="demo"  v-if="tag">
          <p>< <span>template</span> > </p>
            <p><<span>down</span>></p>
          <p> <span class='nbsp'></span> :selectWidth="selectWidth"</p>
          <p> <span class='nbsp'></span>:subject="subject"</p>
          <p> <span class='nbsp'></span>:selectColor="selectColorbg"</p>
          <p> <span class='nbsp'></span>:selectContent="selectContent"</p>
          <p><span class='nbsp'></span>:selectHeight="selectHeight"</p>
          <p> <span class='nbsp'></span>:selectFont="selectFont"</p>
          <p> <span class='nbsp'></span>@changeSelect="datasss"</p>
          <p><<span>/down</span>></p>
          <p><<span>/template</span>></p>
          <p><<span>script</span>></p>
          <p> import down from '@/pages/select/down.vue' </p>
          <!--<p>export default {</p>-->
          <p>export default {</p>
          <p>components: {
            down
            },</p>
          <p>data() {</p>
          <p>  <span class='nbsp'></span> return {</p>
          <p>  <span class='nbsp'></span> selectWidth: {</p>
          <p>  <span class='nbsp'></span> default:200</p>
          <p> },</p>
          <p>  <span class='nbsp'></span> selectHeight: {</p>
          <p>   <span class='nbsp'></span> default:40</p>
          <p>  },</p>
          <p>  <span class='nbsp'></span> selectFont:{</p>
          <p>  <span class='nbsp'></span> default:13</p>
          <p>},</p>
          <p>  <span class='nbsp'></span> subject:['','','','',''],</p>
          <p>   selectColorbg:{</p>
          <p>  <span class='nbsp'></span>  default:'#fff'</p>
          <p>  },</p>
          <p>  selectContent:{</p>
          <p>  <span class='nbsp'></span> text: ""</p>
          <p> <span class='nbsp'></span>  }</p>
          <p>  }</p>
          <p> }</p>
          <p>,methods:{</p>
          <p> <span class='nbsp'></span>  datasss(value){</p>
          <p> <span class='nbsp'></span>  console.log(value)</p>
          <p>  <span class='nbsp'></span> }</p>
          <p> }</p>
          <p><<span>/script</span>></p>
        </div>
      </div>
    <h2>Select Attributes</h2>
    <table class="attr">
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>selectWidth</td>
        <td></td>
        <td>string</td>
        <td>--</td>
        <td>200</td>
      </tr>
      <tr>
        <td>selectHeight</td>
        <td></td>
        <td>string</td>
        <td>--</td>
        <td>40</td>
      </tr>
      <tr>
        <td>selectContent</td>
        <td></td>
        <td>string</td>
        <td>--</td>
        <td></td>
      </tr>
    </table>
    <h2>Select Events</h2>
    <table  class="attr">
      <tr>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>changeSelect</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>move</td>
        <td>hover</td>
        <td>(event: Event)</td>
      </tr>
      <tr>
        <td>out</td>
        <td>hover</td>
        <td>(event: Event)</td>
      </tr>
      <tr>
        <td>triggerOption</td>
        <td>option</td>
        <td>--</td>
      </tr>
    </table>
    <h2>Option Attributes</h2>
    <table class="attr">
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>subject</td>
        <td></td>
        <td>string</td>
        <td>--</td>
        <td>1,2,3,4</td>
      </tr>
      <tr>
        <td>selectColorbg</td>
        <td></td>
        <td>string</td>
        <td>--</td>
        <td>#fff</td>
      </tr>
      <tr>
      <td>selectFont</td>
      <td></td>
      <td>number</td>
      <td>--</td>
      <td>12</td>
    </tr>
    </table>
  </div>
</template>

<script>
  import DlSelect from './components/DlSelect.vue'
export default {
  name: 'Home',
  components: {
    DlSelect
  },
data() {
  return {
    selectWidth: {
      default:200
    },
    selectHeight: {
      default:40
    },
    selectFont:{
      default:13
    },
    subject:['','','','',''],
    selectColorbg:{
          default:'#fff'
       },
    selectContent:{
      text: ""
       },
    tag:false
    }
    }
    ,methods:{
    datasss(value){
      console.log(value)
    },
    handleChangeTag(){
      console.log(this.tag)
      this.tag = this.tag === false ? true : false
    }
  }
  }


</script>

<style lang='scss' scoped>
h1,h2,h5{
  font-weight: 400;
  color: #1f2f3d;
}
h2{
  /*border:1px solid #EBEBEB;*/
  width:20%;
  margin-bottom:50px;
}
.nbsp{
  display: inline-block;
  width:30px;
}
  .select-eg{
    padding: 24px;
    height:88px;
    border:1px solid #EBEBEB;
    width:50%;
  }
  .show-demo{
    border:1px solid #EBEBEB;
    width:50%;
    left:8px;
    top:328px;
    /*text-align: center;*/
    line-height:4px;
  }
.show-demo h5{
  text-align: center;
  cursor:pointer;
  border: 1px solid #ebebeb;
  border-top: none;
  font-size: 14px;
  line-height: 44px;
  text-align: center;
  color: #5e6d82;
}
  .demo{
    width:100%;
    padding:24px;
    border:1px solid #EBEBEB;
    background:#FAFAFA;
  }
  .demo p{
    color:#3182BD;
  }
  .attr{
    border-collapse:collapse;
    margin-bottom:80px;
  }
  .attr tr td{
    border-bottom: 1px solid #DCDFE6;
    padding:24px;
  }
.title>h3{
  font-size: 28px;
  font-weight: 400;
  color: #1f2f3d;
  margin-bottom: 15px;
}
.title>p{
  font-size: 14px;
  color: #5e6d82;
  line-height: 20px;
  margin-bottom: 15px;
}
</style>



